@charset "UTF-8";
:root {
  --font-family-base: "Noto Sans JP", sans-serif;
  --font-family-second: "Noto Sans", "Noto Sans JP", sans-serif;
  --font-family-third: "gill-sans-nova", "Noto Sans", "Noto Sans JP", sans-serif;
  --font-family-fourth: "dnp-shuei-gothic-kin-std", sans-serif;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
}

:root {
  --black: #000;
  --white: #fff;
  --main-orange: #EE8A43;
  --main-blue: #036EB8;
  --main-yellow: #FCBF42;
  --main-pink: #F9B1BF;
  --main-beige: #DFDBBB;
  --main-green: #428B8D;
  --main-white: #F9F9F9;
  --bg-pink: rgba(249, 177, 191, 0.40);
  --bg-beige: rgba(223, 219, 187, 0.85);
  --bg-green: rgba(66, 139, 141, 0.75);
  --bg-orange: rgba(238, 138, 67, 0.20);
  --bg-blue: rgba(3, 110, 184, 0.20);
  --bg-gray: #EDEBDC;
}

:root {
  --z-index-header: 200;
  --z-index-to-top: 200;
  --z-index-drawer: 1000;
  --z-index-hamburger: 1001;
}

:root {
  --cq-ref-mobile: 390;
  --cq-ref: 1440;
  --fluid-basis-mobile: (100vw - 320px) / (390 - 320);
  --fluid-basis: (100vw - 768px) / (1440 - 768);
  --fluid-basis-2: (100vw - 1440px) / (1800 - 1440);
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
  --ease-out: cubic-bezier(.23, 1, .32, 1);
}

body {
  color: var(--black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

@media (max-width: 1250px) {
  .tablet-br {
    display: block;
  }
}
@media (min-width: 1251px) {
  .tablet-br {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 390px) {
  html {
    font-size: 4.1025641026vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.3333333333vw;
  }
}
@media (min-width: 1200px) {
  html {
    font-size: 16px;
  }
}

